.space__sky {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.stars, .twinkling {
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  width:100%;
  height:100%;
  display:block;
}

.stars {
  background:#000 url(../assets/stars.png) repeat top center;
  z-index:0;
}
.twinkling{
  background:transparent url(../assets/twinkling.png) repeat top center;
  z-index:1;
  animation:move-twink-back 400s linear infinite;
}

@keyframes move-twink-back {
  from {background-position:0 0;}
  to {background-position:-10000px 5000px;}
}